.grid12 {
  display: grid;
  grid-template-columns: [center-start] repeat(12, [col-start] minmax(0px, 73px) [col-end]) [center-end];
  column-gap: 24px;
  align-items: center;
}

.grid12-3 {
  grid-column: span 3;
}

.grid12-5 {
  grid-column: span 5;
}

.grid12-6 {
  grid-column: span 6;
}

.grid12-7 {
  grid-column: span 7;
}

.grid12-10 {
  grid-column: 2/span 10;
}

.flex {
