.header .hamburger__button {
  display: none;
}
.header .header--container:not(.mobile-navi-open) .nav a:hover {
  color: #0064a3;
}
.header .header--container:not(.mobile-navi-open) .nav .main-menu > li {
  line-height: 50px;
  display: inline-block;
  position: relative;
}
.header .header--container:not(.mobile-navi-open) .nav .main-menu > li:not(:last-child) {
  margin-right: 2.5rem;
}
.header .header--container:not(.mobile-navi-open) .nav .main-menu > li > a {
  position: relative;
}
.header .header--container:not(.mobile-navi-open) .nav .main-menu > li > a::before {
  content: "";
  position: absolute;
  bottom: -0.5em;
  left: 0;
  width: 100%;
  background: #0064a3;
  height: 4px;
  border-radius: 10px;
  opacity: 0;
}
.header .header--container:not(.mobile-navi-open) .nav .main-menu > li.menu-item-has-children > a::after {
  content: "";
  display: inline-block;
  width: 1rem;
  aspect-ratio: 1;
  background-image: url("/asset/icons/chevron-down.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  margin-left: 0.5rem;
  transform: translateY(3px);
  transition: all 0.5s;
}
.header .header--container:not(.mobile-navi-open) .nav .main-menu > li.active.menu-item-has-children > a::after {
  transform: rotate(180deg);
  transition: all 0.8s;
}
.header .header--container:not(.mobile-navi-open) .nav .main-menu > li.menu-item-has-children > a::before {
  width: calc(100% - 1.5rem);
}
.header .header--container:not(.mobile-navi-open) .nav .main-menu > li.current-menu-ancestor a:before, .header .header--container:not(.mobile-navi-open) .nav .main-menu > li.current-menu-item a:before {
  opacity: 1;
}
.header .header--container:not(.mobile-navi-open) .nav .main-menu > li.active > ul {
  visibility: inherit;
}
.header .header--container:not(.mobile-navi-open) .nav ul.menu-depth-1 {
  font-size: 18px;
  /* Hide Dropdowns by Default */
  visibility: hidden;
  /* Dropdown box decoration */
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  background-color: white;
  position: absolute;
  margin-top: 1rem;
  left: -1.5em;
  /* drop down li, padding, margin, underlin */
}
.header .header--container:not(.mobile-navi-open) .nav ul.menu-depth-1 .current-menu-item a,
.header .header--container:not(.mobile-navi-open) .nav ul.menu-depth-1 .current-menu-ancestor a {
  position: relative;
}
.header .header--container:not(.mobile-navi-open) .nav ul.menu-depth-1 .current-menu-item a::before,
.header .header--container:not(.mobile-navi-open) .nav ul.menu-depth-1 .current-menu-ancestor a::before {
  display: block;
  background-color: #0064a3;
  border-radius: 20px;
  height: 1rem;
  width: 3px;
  content: "";
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: -1rem;
}
.header .header--container:not(.mobile-navi-open) .nav ul.menu-depth-1 li {
  min-width: 130px;
  padding: 0.8rem 0px;
  margin: 0px 1.5em;
  line-height: 1.5em;
}
.header .header--container:not(.mobile-navi-open) .nav ul.menu-depth-1 a {
  white-space: nowrap;
}
.header .header--container:not(.mobile-navi-open) .nav ul.menu-depth-2 {
  display: none;
}
